.preloader {
  z-index: 2000;
  background: #4e0e0e;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.preloader__string {
  width: 100px;
  height: 100px;
  text-align: center;
  margin-top: -35px;
  margin-left: -35px;
  animation: preloader-rotate 2s linear infinite;
  position: relative;
  top: 50%;
  left: 50%;
}

.preloader__item {
  width: 35px;
  height: 35px;
  background-color: #fff;
  border-radius: 100%;
  animation: preloader-bounce 2s ease-in-out infinite;
  display: inline-block;
  position: absolute;
  top: 0;
}

